@IonicPage()
@Component({
  selector: 'page-welcome2',
  templateUrl: 'welcome2.html'
})
export class Welcome2Page {
  showBtn: boolean = false;
  deferredPrompt;
  loading: any;
  pag0:number=0;
  estilomenu:string;
  APP:String;
  public vdata  :any=[];
inter:string;
  icon2: string //= './assets/img/splashbg.png';
  
  public Form: FormGroup;
  constructor(public alertCtrl: AlertController,  public navParams 	: NavParams,   public events: Events, public formBuilder: FormBuilder, public storage: Storage,public navCtrl: NavController, public menu: MenuController, 
    public loadingCtrl: LoadingController, public authService: Auth
    ,public msj: NotificationProvider, public translateService: TranslateService
   // ,private barcodeScanner:BarcodeScanner
  ) { 
    this.menu.enable(false);  
    
    this.Form = formBuilder.group({
      email: ['', Validators.compose([Validators.maxLength(50), Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/), Validators.required])],
      password: ['', Validators.compose([Validators.maxLength(30), Validators.pattern( /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/), Validators.required])]
    
  });
//WebApp YUHUYUHU
  this.translateService.get('APPX').subscribe((value) => {
    this.APP=value
    this.estilomenu=value
      if(value=='WebApp Usacenlinea')
      {
        this.inter=value
        this.icon2='./assets/img/splashbg.png'
       
      }
      else{
        this.inter=value
        this.icon2='./assets/img/splashbg.png'
       
      
      }
  })

  }

  ionViewWillEnter(){
    window.addEventListener('beforeinstallprompt', (e) => {
      // Prevent Chrome 67 and earlier from automatically showing the prompt
      e.preventDefault();
      // Stash the event so it can be triggered later on the button event.
      this.deferredPrompt = e;
       
    // Update UI by showing a button to notify the user they can add to home screen
      this.showBtn = true;
    });
     
    //button click event to show the promt
             
    window.addEventListener('appinstalled', (event) => {
     
    });
     
     
    if (window.matchMedia('(display-mode: standalone)').matches) {
    
    }
  }
 
  add_to_home(){
    debugger
    // hide our user interface that shows our button
    // Show the prompt
    this.deferredPrompt.prompt();
    // Wait for the user to respond to the prompt
    this.deferredPrompt.userChoice
      .then((choiceResult) => {
        if (choiceResult.outcome === 'accepted') {
          
        } else {
          
        }
        this.deferredPrompt = null;
      });
  };

  ionViewDidLoad() {
    this.Form.get('email').setValue(this.navParams.data.user);
    this.Form.get('password').setValue(this.navParams.data.pass);
  
  /*    this.authService.checkAuthentication().then((res) => {
      
      this.storage.get('hasSeenTutorial').then((hasSeenTutorial) => {
        if (hasSeenTutorial) {
          
          this.poneperfil();
          this.pag0=1;
          this.navCtrl.push(MainPage,{uno: "Tab1Root", dos: "Tab3Root" });
        
          
        } else {
         
          this.navCtrl.push('TutorialPage');
          this.pag0=0;
        //  this.rootPage = TutorialPage;
        }
       
      });
    }, (err) => {
       // console.log("Todavía no autorizado");
    
    });

    */
}

showLoader(){
    this.translateService.get('ESPERE').subscribe((value) => {
      this.loading = this.loadingCtrl.create({
        content: value
    });
    })
    this.loading.present();
    this.loading.dismiss();
} 

 isEmpty(obj) {
  for(var key in obj) {
      if(obj.hasOwnProperty(key))
          return false;
  }
  return true;

}
  
  
dahelp() {    

  this.translateService.get('APPX').subscribe((value) => {
    this.APP=value
      if(value=='WebApp Usacenlinea')
      {
        this.navCtrl.push('HelpviewPage',{idempresa:this.APP});
      }
      else{
        this.navCtrl.push('HelpviewPage',{idempresa:this.APP});
      
      }
  })


}
  
dalink(op) { 
  //this.msj.showError3('Link QR',op);
  this.authService.abrelinkinterno3(op,'_blank','',0)
  
}

   

public poneperfil(): void {
         this.storage.set('username', this.Form.get('email').value.trim());
         this.authService.getReg(this.Form.get('email').value.trim() + '/emailsolo/' +this.APP,'personals').then((data) => {
             
                  this.storage.set('tiposus', data[0].tiposuscriptor.nombre + ',' + data[0].tiposuscriptor.cobroparqueos+ ',' + data[0].cui+ ',' + data[0]._id+ ',' + data[0].nombre+ ','+data[0].nov+ ','+data[0].role+','+data[0].unidad._id+ ',' + data[0].controlacceso + ','+ data[0].foto);
           
                  this.events.publish('nombre:cambia', data[0].nombre);
                  this.events.publish('foto:cambia', data[0].foto);
                  this.events.publish('rolx:otro', data[0].role);
                 
                  
                  if(this.APP!='WebApp Usacenlinea')
                  {
                    this.navCtrl.push(MainPage,{uno: "Tab5Root", dos: "Tab5Root" });
                  }
                  else
                  {

                  if(data[0].role=='lluviaideas')
                  {
                    this.navCtrl.push(MainPage,{uno: "Tab4Root", dos: "Tab4Root" });  
                  }
                  else
                  {
                    if(this.pag0==0)
                    {
                      this.navCtrl.push(MainPage,{uno: "Tab1Root", dos: "Tab3Root" });
                    }
                  
                  }
                }
                  
              }, (err) => {
                  if(err.error=='NO EXISTE REGISTRO')
                  {
                    this.events.publish('nombre:cambia', '');
                    this.events.publish('foto:cambia', '');
                    this.translateService.get('PROFILE_ERROR').subscribe((value) => {
                      this.msj.showError3('Error',value);
                      })
                    if(this.pag0==0)  {   this.navCtrl.push(MainPage,{uno: "Tab3Root", dos: "Tab1Root" });  }
                  }
                
              });
}
   
login(){
        let credentials = {
          idempresa:this.APP,
            email: this.Form.get('email').value.trim(),
            password: this.Form.get('password').value.trim(),
            bitacora:{idempresa:this.APP,idafiliado:'',email:this.Form.get('email').value.trim(),permiso:'Ingreso',accion:'Ingreso al sistema'}
        };

        this.authService.createAPITOKEN(credentials,'/api/auth/login').then((result) => {
        var autt:any=result;  
        if(autt.user._id=='-1')
        {
          this.msj.showError3('Precaución',autt.user.role);
        }
        else
        {
          if(autt.user._id=='-11')
          {
            this.msj.showError3('Precaución',autt.user.role);
          }
          else
          {

            if(autt.user.estadoemail=='0')
            {
               this.msj.showError3('Precaución','Por favor ingresa a tu correo y confirma tu suscripción ');

            }
            else
            {
              this.poneperfil();
            }
            
            
          }

        }

       
           
        
  
    }, (err) => {
      this.msj.showError3('Precaución',err.error);
    });
}
passwordType: string = 'password';
passwordIcon: string = 'eye-off';

showHide() {
  this.passwordType = this.passwordType === 'text' ? 'password' : 'text';
  this.passwordIcon = this.passwordIcon === 'eye-off' ? 'eye' : 'eye-off';
}

launchSignup(){


  this.translateService.get('APPX').subscribe((value) => {
    this.APP=value
      if(value=='WebApp Usacenlinea')
      {
        this.navCtrl.push('SignupPage',{record2:{APP:this.APP}});
      }
      else{
        this.navCtrl.push('SignupPage',{record2:{APP:this.APP}});
      
      }
  })


  
  
   
}


noclave(){
 
     
  let prompt = this.alertCtrl.create({
    title:' Reiniciar clave',
    message: ' Ingresa EMAIL y CUI registrado en la plataforma, para poder validar información:',
    inputs: [
      {  
        name: 'email',
        placeholder:'Ingrese EMAIL registrado',
        value:this.Form.get('email').value.trim()
      },
      {
        name: 'cui',
        placeholder:'Ingrese CUI registrado',
        value:''
      }
    ],
    buttons: [
      {   
        text: 'Cancelar'
      },
      {
        text: 'Aceptar',  
        handler: todo => {   
 
              if(todo){
      
         if(todo.email==''  ||  todo.cui=='' )
         {
          this.msj.showError3('Advertencia','Ingrese un CORREO o CUI valido');
          return;

         }


         let credentials = {
           idempresa:this.APP,
          email         	:  todo.email   	,
          cui: todo.cui,
          bitacora:{idempresa:this.APP,idafiliado:'',email: todo.email,permiso:'Reinicia',accion:'Reinicia clave'}
        };
      
        const loading = this.loadingCtrl.create({
          spinner: 'bubbles',
          content: "Procesando información..."
        });
        loading.present();
        this.authService.createAPISINTOKEN(credentials,'/api/auth/register4').then((result) => {
        
        var arr:any=[];
       arr=result;
          loading.dismiss();
          var clavexx=arr
          clavexx=clavexx.replace('"','')
          clavexx=clavexx.replace('"','')
          clavexx=clavexx.replace('"','')
          this.msj.showError3('Exito',' Tu nueva contraseña es: ' + clavexx + ' ' );
     
          this.Form.get('email').setValue(todo.email);
          this.Form.get('password').setValue(clavexx);
          this.login();
          

       
          
        }, (err) => {
          this.msj.showError3('Advertencia',err.error);
          loading.dismiss();
      
        });  


 


              }


        }
      }
    ]
  });

  prompt.present();

}

  
  
}



